.card-title{
  font-size: 15px;
  text-transform: capitalize;
  font-weight: bold;
  margin-top: 5px;
}
.card-img{
  
  border-top-left-radius: 10px;
  margin-right: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
