Button{
  padding: 6px;
  background-color: #D8BFD8;
  border-radius: 5px;
  border: none;
}
.link-button{
  padding: 8px;
  border-radius: 5px;
  background-color:#778899;
  display: inline-block;
  cursor: pointer;
  border: none;
}
.link-button:hover{
  background-color: lightgray;
}
